import React, { Component } from "react";
import face from '../pics/ezazf.png';
import { slide as Menu } from 'react-burger-menu';
import ola from '../pics/ola.png';

const BIRTHDATE = new Date(1999, 9, 18); // Your birthdate

class Age extends Component {
  constructor() {
    super();
    this.state = {
      age: this.calculateAge(),
    };
  }

  calculateAge() {
    const currentDate = new Date();
    const ageInMilliseconds = currentDate - BIRTHDATE;
    const ageInYears = ageInMilliseconds / (365.25 * 24 * 60 * 60 * 1000); // Account for leap years
    return ageInYears.toFixed(9); // Display age with 9 decimal places (months)
  }

  componentDidMount() {
    // Update the age every second for a smooth effect
    this.ageIncrementInterval = setInterval(() => {
      this.setState({
        age: this.calculateAge(),
      });
    }, 10); // 1000 milliseconds = 1 second
  }

  componentWillUnmount() {
    // Clear the interval when the component is unmounted to prevent memory leaks
    clearInterval(this.ageIncrementInterval);
  }

  render() {
    return (
      <div>
            <div class="right">
    <Menu right bubble width={ '100%' }>
        {/* <a id="home" className="menu-item" href="/">Home</a>
        <a id="about" className="menu-item" href="/about">About</a>
        <a id="contact" className="menu-item" href="/contact">Contact</a> */}
        <div class="container" style={{outline:"none"}}>

<div align="center">
<img src={ola} style={{width:"20%"}} alt="Ezazul Hoque" />

<div class="header__item">
            <a href="#" style={{textDecoration:"none", color:"inherit"}}>Home</a>
          </div>
          <div class="header__item">
          <a href="/#/Blogs" style={{textDecoration:"none", color:"inherit"}}>Essays</a>
          </div>
          <div class="header__item">
          <a href="/#/resume" style={{textDecoration:"none", color:"inherit"}}>Resume</a>
          </div>
          <div class="header__item">
          <a href="mailto:ezazulhoqueniloy@gmail.com" style={{textDecoration:"none", color:"inherit"}}>Contact</a>
          </div>
</div>
        </div>
      </Menu>
    </div>

          <div class="container">
            <img src={face} style={{width:"30%", marginLeft:"35%"}} alt="Ezazul Hoque" />

                <div class="row">
                    <div class="col-sm-12">
                        <div>
                        <div align="center"class="mt-5 h1">
            {/* Display the age with 9 decimal places (months) */}
            Age: {this.state.age} years
          </div>
                        </div>
                    </div>
                </div>
            </div>
    </div>
      
    );
  }
}

export default Age;

